import React, { Component } from 'react'
import { Field } from 'redux-form'
import { SearchMap } from 'Shared/components/FormElements'
import withTranslations from 'Shared/hoc/withTranslations'
import ProfileStepSection from 'Shared/components/ProfileStepSection'

class Location extends Component {
  render () {
    const { t } = this.props

    return (
      <div>
        <ProfileStepSection
          label={t('jobOffer.postAJob.location.label')}
          text={t('jobOffer.postAJob.location.text')}
        />
        <Field
          component={SearchMap}
          placeholder={t('forms.location.placeholder')}
          name='location'
        />
      </div>
    )
  }
}

export default withTranslations(Location)



